import Vue from 'vue'
import Router from 'vue-router'




Vue.use(Router)

const routes = [
 {
  path:'',
  component:()=>import('./../../src/layoutes/client/ClientLayout.vue'),
  children:[

    {
      path: '',
      name: 'home',
      component: () => import('../pages/HomePage.vue')
    },
    {
      path: '/register-form',
      name: 'Register Form',
      component: () => import('../pages/RegisterForm.vue')
    },
    {
      path: '/signin-form',
      name: 'SigninForm',
      component: () => import('../pages/SigninForm.vue')
    },
    {
      path: '/service_list',
      name: 'Servicelist',
      component: () => import('../pages/Servicelist.vue')
    },
  ]

 },
{
  path:'/admin/dashboard',
  component:()=>import('./../../src/layoutes/admin/AdminLayout.vue'),
  children:[
    {
      path: "",
      name: "Dashboard",
      component: () => import('../pages/Admin/Dashboard.vue')
      
    },
    {
      path:'/admin-login',
      name:"Adminlogin",
      component: () => import('./../components/Admin/Adminlogin.vue')
    },
    {
      path:'/admin/user-data-list',
      name:'UserDataList',
      component:()=> import('./../pages/Admin/UserDataList.vue')
    },
    {
      path:'/admin/user-add',
      name:'AddUser',
      component:()=> import('./../components/Admin/AddUser.vue')
    }
  ]
}


]






export default new Router({

  routes
})



